.wrapper {
  max-width: 1500px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 auto;
}

.container-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  row-gap: 10px;
  column-gap: 15px;
  margin: 20px auto;
}

.btn-field {
  display: flex;
  justify-content: space-around;
  margin: 25px;
}
